.lazyloaded-image-wrap {
  position: relative;
  overflow: hidden;
}

.lazyloaded-image-wrap.lazyloaded--loaded .lazyloaded-image-blurred {
  opacity: 0;
}

.lazyloaded-image-wrap .lazyloaded-image {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0px;
  left: 0px;
  opacity: 0;
  transition: opacity 250ms cubic-bezier(0.86, 0, 0.07, 1);
  transition-delay: 100ms;
  z-index: 2;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

  &:not([data-background="true"]) {
    width: 100%;
    height: auto;
  }
}

.lazyloaded-image-wrap .lazyloaded-image.lazyloaded--loaded {
  opacity: 1;
}

.lazyloaded-image-wrap .lazyloaded-image-blurred {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0px;
  left: 0px;
  transition: opacity 250ms cubic-bezier(0.86, 0, 0.07, 1);
  opacity: 1;
  transition-delay: 350ms;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  transform: scale(1.05);
  filter: blur(5px);
  z-index: 1;

  &.image-element {
    width: 100%;
    height: auto;
    position: static;
  }
}

.lazyloaded-image-wrap img.noscript {
  position: absolute;
  z-index: 2;
  width: 100%;
  object-fit: cover;
  top: 0px;
  left: 0px;
}
